import config from "../../assets/js/conf/config";
import {request} from "../../assets/js/utils/request";

//
export function getClassifyData() {
    return request(config.baseApi+ "/home/category/menu?token=" +config.token);
}
////
export function getClassifyContentData(cid="") {
    return request(config.baseApi+ `/home/category/show?cid=${cid}&token=` +config.token);
}
//
export function getdetailsData(gid="") {
    return request(config.baseApi+ `/home/goods/info?gid=${gid}&type=details&token=` +config.token);
}
//
export function getReviewData(gid="") {
    return request(config.baseApi+ `/home/reviews/index?gid=${gid}&token=${config.token}&page=1`);

}
//
export function getspecsData(gid="") {
    return request(config.baseApi+ `/home/goods/info?gid=${gid}&type=spec&token=` +config.token);
}
//
export function getAddFavData(data) {
    return request(config.baseApi + "/goods/fav?uid="+data.uid+"&gid="+data.gid+"&token="+ config.token)
}
//
export function getUserFavData(data) {
    return request(config.baseApi +"/user/fav/index?uid="+data.uid+"&token="+config.token+"&page="+data.page+"")
}
//
export function getDelUserFavData(data) {
    return request(config.baseApi + "/user/fav/del?uid="+data.uid+"&fid="+data.fid+"&token="+config.token,"post")
}